@import '~theme.scss';

.root {
  @extend .container;
  @include spacing(margin-bottom, 160);
}

.tag {
  @include font-sizing(16);
  font-weight: bold;
  letter-spacing: 1.28px;
  color: #7f7592;
  @include spacing(margin-bottom, 16);
}

.title {
  @include font-sizing(48);
  font-weight: bold;
  color: #ffffff;
  @include spacing(margin-bottom, 56);
}

.articles {
  display: flex;
  justify-content: space-between;
}

.articleAnchor {
  position: relative;
  width: 490px;
  &:hover {
    > img {
      opacity: 0.5;
    }
  }
  > img {
    width: 100%;
  }
  .foreground {
    position: absolute;
    @include spacing(bottom, 32);
    @include spacing(left, 32);
    .text {
      @include spacing(margin-top, 16);
      @include font-sizing(20);
      font-weight: bold;
      line-height: 1.4;
      color: #ffffff;
    }
  }
}