@import "~theme.scss";

.root {
  @extend .container;
  @include spacing(margin-bottom, 160);
  display: flex;
  justify-content: space-between;
  > div {
    width: 50%;
  }

  > div > .tag {
    @include spacing(margin-bottom, 16);
    @include font-sizing(16);
    color: #7f7592;
    font-weight: bold;
  }
  > div > .title {
    @include font-sizing(40);
    color: #ffffff;
    font-weight: bold;
  }
  @media (max-width: $breakpoint-middle) {
    flex-direction: column;
    > div {
      width: 100%;
    }
    > div > .title {
      @include spacing(margin-bottom, 56);
      text-align: center;
    }
    > div > .tag {
      text-align: center;
    }
  }
}

.reason {
  display: flex;
  img {
    width: 150px;
    height: auto;
    @include spacing(margin-right, 32);
  }
  .text {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: auto;
    > div + div {
      @include spacing(margin-top, 8);
    }
    .title {
      font-family: Roboto;
      @include font-sizing(20);
      font-weight: bold;
      color: #ffffff;
    }
    .description {
      font-family: Roboto;
      @include font-sizing(20);
      font-weight: bold;
      color: #7f7592;
    }
  }
}

.reason + .reason {
  @include spacing(margin-top, 56);
}